$primary-color-dark:   #1976D2;
$primary-color:        #2196F3;
$primary-color-light:  #BBDEFB;
$primary-color-text:   #FFFFFF;
$accent-color:         #FF9800;
$primary-text-color:   #212121;
$secondary-text-color: #757575;
$divider-color:        #BDBDBD;

@mixin background-img($src) {
    background-image: url($src);
    background-position: center ;
    background-repeat: no-repeat;
    background-size: cover;
}

html {
    font-size: 10px;
}
body {
    font-size: 16px;
    background-color: $divider-color;
    color: $secondary-text-color;
    .container {
        padding: 0;
    }
    // заголовок
    .title {overflow: hidden;
    text-align: center;
    h4 {
        color: $secondary-text-color;
        margin: 2rem 0;
        padding: 2rem;
        font-size: 1.5em;
        display: inline-block;
        vertical-align: middle;
        font-weight: bold;
    }
    &:before,
    &:after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 20%;
        height: 1rem;
        background-color: $primary-color;
        position: relative;
        opacity: 0.5;
    }
    &:before {
        margin-left: -100%;
        left: 1.5rem;
    }
    &:after {
        margin-right: -100%;
        right: 1.5rem;
    }
}

    // Кнопки
    .btn {
        border: 2px solid $primary-color;
        background-color: transparent;
        color: $primary-color;
        border-radius: 0.5rem;
        font-size: 1em;
        padding: 1rem 2rem;
        &:hover {
            background-color: $primary-color;
            color: $primary-color-text;           
        }
    }
    .btn-inverse {
        border: 2px solid $primary-color;
        background-color: transparent;
        color: $primary-color;
        border-radius: 0.5rem;
        font-size: 1em;
        padding: 1rem 2rem;
        &:hover {
            background-color: $primary-color;
            color: $primary-color-text;           
        }
    }
    // Меню
    .navbar {
        padding: 0.3rem 5rem;
        border: 1px solid $primary-text-color;
        .number-phone {
            color: $primary-color;
        }
        .navbar-nav {
        li.nav-item > a.nav-link {
            color: $primary-text-color;
            &:hover {
                color: $primary-color;
            }
        }
        li.active > a.nav-link {
            color: $primary-color;
        }
    }
    }
    // Шапка
    header {
        @include background-img('../img/');
        padding: 15rem 3rem;
        p {
            color: $secondary-text-color;
        }
        h4 {
            font-size: 20px;
            font-weight: bold;
            color: $secondary-text-color;
            > span {
                color: $primary-color;
            }
        }
        form {
            border: double 1rem $primary-color-text;
            div {
                background-color: rgba($primary-color-text, 0.95);
                padding: 1.5rem;
            }
            span {
                color: $primary-color;
                font-weight: bold;
                font-size: 1em;               
            }
            input {
                width: 100%;
                padding: 1rem;
                background-color: transparent;
                border: 2px solid $primary-color;
                border-radius: 0.5rem;
                margin-top: 1.5rem;
                margin-bottom: 1.5rem;
                &::-webkit-input-placeholder {
                color: $primary-color;
                }
                &:-moz-input-placeholder {
                    color: $primary-color;
                }
                &:-ms-input-placeholder {
                    color: $primary-color;
                }
                
            }
        }
    }
    // категории
    .popcat {
        @include background-img('../img/');
        padding-bottom: 4rem;
 
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            text-decoration: none;
            p {
                padding: 0;
                margin: 0;
                font-weight: bold;
                color:$primary-text-color;
                text-shadow: 0 0 3px $primary-text-color;
                position: absolute;
                font-size: 1.5em;
                z-index: 100;
                transition: all 0.5s;
            }
            img {
                transition: all 0.5s;
            }
            &:hover {
                img {
                background-color: $primary-color-light;
                transform: scale(1.3);
                }
                p {
                transform: scale(0.9);

                }
            }
        }
    }
    // Преимущества
    .adv {
        color: $primary-color-text;   
        @include background-img('../img/');
        
        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            

            background-image: linear-gradient(to left, $primary-color-dark 0%, $primary-color 50%, $primary-color-light 100%);
            opacity: 0.5;
        }
        .row {
            padding: 3rem 0;
        } 
        h6 {
            margin: 1rem 0;
            font-weight: bold;
            font-size: 1em;
        }
        p {
            font-size: 0.7em;
        }
    }
    // Товары
    .popgoods {
        @include background-img('../img/');

    }
}